.gameboard {
    /* background-color: aqua; */
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}

.instructions {
    width: 70%;
    border: 1px solid rgba(255, 0, 0, 0.507);
    border-radius: 10px;
    background-color: #ffc8fe;
    text-align: left;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    padding-top: 1rem;
    padding-bottom: 1rem;
    color: black;
}

.instructions p {
    margin-top: 0;
    margin-left: 2rem;
    margin-right: 2rem;
    font-size: larger;
}

.instructions button {
    width: 80px;
    height: 45px;
    margin-left: auto;
    margin-right: auto;
}


.scoreboard-container {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 5px solid rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    padding-left: 1rem;
    padding-right: 1rem;
}

.character-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.character {
    margin: .5rem;
    width: 21%;
    height: 300px;
    border: 5px solid black;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;

}

.character-img {
    width: 100px;
}

.character h3 {
    text-align: end;
}

.gameover {
    position: absolute;
    right: 0;
    
    width: 100%;
    height: 100%;
    background-color: rgba(255, 0, 0, 0.363);
}